body, html{
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face{
  font-family: druk;
  src: url(../assets/DrukTextWide-Medium.ttf);
}

a{
  text-decoration: none;
  display: block;
  font-size: 12px;
  font-weight: 100;
  text-transform: uppercase;
}

button{
  border: none;
}

h1, p, a, button{
  font-family: druk;
}

h1{
  font-size: 6vw;
  text-transform: uppercase;
}

.cta{
  padding: 20px 40px;
  background: #000;
  color: #fff;
  display: block;
  width: auto;
  margin: auto;
}

.para{
  font-size: 25px;
  opacity: .5;
}


.row{
  display: block;
  overflow: hidden;

  & > span{
    display: block;
    white-space: nowrap;
  }
}






//Menu

nav{
  width: 100%;
  padding: 8vh 6vw 0 6vw;
  display: flex;
  justify-content: space-between;
  position: fixed;
  z-index: 100;
}

.nav_desktop{
  display: none;
  position: relative;
  width: 100%;

  div{
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translate(-55%, 0);

    a{
      padding: 0 20px;
      color: #000;
    }
  }

  & > a{
    position: absolute;
    right: 0;
  }

  .cta{
    transform: translate(0, -36%);
  }
}

#nav_mobile{
  width: 100vw;
  height: 100vh;
  color: #000;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;

  div{
    padding: 20vh 5vw;
  }

  video{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: .4s;
  }

  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    transition: .4s;
  }

  & > div > a > span{
    display: block;
    overflow: hidden;
  }

  & > div > a > span > span{
    display: block;
    transition: .4s;
  }

  a{
    color: #000;
    margin: 15px 0;
    font-size: 24px;
  }
}

.nav_mobile-hidden{
  pointer-events: none;

  video{
    opacity: 0;
  }

  &::before{
    transform: translate(0, -100%);
  }

  & > div > a > span > span{
    transform: translate(0, 100%);
  }
}

.nav_mobile-visible{
  pointer-events: all;

  video{
    opacity: 1;
  }

  &::before{
    transform: translate(0, 0);
  }

  & > div > a > span > span{
    transform: translate(0, 0);
  }
}


#menu-toggle {
  width:20px;
  height:30px;
  position:relative;
  cursor:pointer;
  &:hover {
    background:rgba(255,255,255,.8);
  }

  span{
    display: block;
    background-color: #000;
    transition: .4s;
  }
  #hamburger {
    position:absolute;
    height:100%;
    width:100%;
    span {
      widows: 80%;
      height:2px;
      margin:3px 0;
      &:nth-child(1){
        transition-delay:.5s;
      }
      &:nth-child(2){
        transition-delay:.625s;
      }
      &:nth-child(3){
        transition-delay:.75s;
      }
    }
  }
  #cross {
    position:absolute;
    height:100%;
    width:100%;
    transform:rotate(45deg);
    span{
      &:nth-child(1){
        height:0%;
        width:2px;
        position:absolute;
        top:-12%;
        left:30%;
        transition-delay:0s;
      }
      &:nth-child(2){
        width:0%;
        height:2px;
        position:absolute;
        left:-6%;
        top:24%;
        transition-delay:.25s;
      }
    }
  }
}

#menu-toggle.open {
  #hamburger {
    span {
      width:0%;
      &:nth-child(1){
        transition-delay:0s;
      }
      &:nth-child(2){
        transition-delay:.125s;
      }
      &:nth-child(3){
        transition-delay:.25s;
      }
    }
  }
  #cross {
    span {
      &:nth-child(1){
        height:80%;
        transition-delay:.625s;
      }
    }
    span {
      &:nth-child(2){
        width:80%;
        transition-delay:.375s;
      }
    }    
  }
}


.cookies{
  position: fixed;
  bottom: 5vh;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 20px 40px;
  border: 3px solid #000;
  background: #fff;
  display: flex;
  align-items: center;
  z-index: 8;

  p{
    color: #000;
    white-space: nowrap;
  }

  a{
    background: #000;
    color: #fff;
    padding: 10px 15px;
    margin-left: 20px;
  }
}



// MAIN CONTENT

//BANNER

.banner{
  width: 100vw;
  padding: 25vh 0;
  position: relative;
  text-align: center;

  
  h1{
    margin-bottom: 40px;
  }

  button{
    margin-top: 90px;
  }

  video{
    position: relative;
    z-index: -1;
    width: 100%;
  }
}


.feat-section{
  padding: 15vh 0;
  width: 100%;
  overflow-x: hidden;
  text-align: center;
  position: relative;
  margin: 5vh 0;
  
  & > div{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  & > div > div{
    flex: 0 1 49%;
  }

  .feat_video{
    overflow: hidden;
    min-width: 375px;
    max-width: 59%;

    video{
      width: 130%;
    }
  }

  .--video-right{
    margin-right: -3vw;
    video{
      transform: translate(-23%, 0);
    }
  }

  .feat-logo{
    position: absolute;
    width: 28vw;
    z-index: 2;
  }

  .--top{
    top: 2vh;
    right: 0;
  }

  .--bottom{
    bottom: 2vh;
    left: 0;
  }
}


.payment{
  text-align: center;
  margin-top: 20vh;

  .text-anim{
    &:first-child > span{
      font-size: 18vw;
    }
  }

  video{
    transform: translate(-12%, -20%);
    width: 120%;
    position: relative;
    z-index: -1;
  }
}


.sliders{
  text-align: center;
  overflow-x: hidden;
  padding-bottom: 40vh;
  position: relative;

  .text-anim{
    margin-bottom: 20vh;
    color: #fff;
  }

  .slider-logos{
    overflow: hidden;

    & > div{
      width: 100%;
    }

    div{
      display: flex;
      align-items: center;
  
      div{
        min-width: 100%;

        img{
          width: 33.33%;
          margin: 20px;
        } 
      }
    }
  }

}

.pla{
  margin-top: 40vh;
}

.pre{
  margin-bottom: 40vh;
}

.slider-logos{
  transform: scale(1.5);
}

.slider-logos div:nth-child(even){
  margin-left: 32%;
}


.pre{
  text-align: center;

  .pre-text{
    display: block;
    padding-bottom: 15vh;

    & > div:last-child{
      width: 64%;
      margin: -12vh auto 0 auto;
    }
  }

  .cta{
    background-color: #fff;
    color: #000;
    padding: 40px 80px;
  }
}


.footer{

  .footer-heading{
    position: relative;
    z-index: 2;

    h1{
      font-size: 8vw;
    }
  }

  .footer-video{
    margin-top: -30%;
    width: 100%;
    overflow-x: hidden;
    position: relative;
    z-index: -1;

    video{
      width: 180%;
      transform: translate(-20%, 0);
    }
  }

  .footer-form{
    transform: translate(0, -110%);

    form{
      display: flex;
      width: 90%;
      margin: auto;

      input{
        width: 55%;
        font-family: druk;
        background-color: #F5F5F5;
        color: #444;
      }

      button{
        width: 45%;
        background-color: #000;
        color: #fff;
      }

      input, button{
        padding: 25px 35px;
        border: none;
        
      }
    }
  }


  p{
    font-size: 10px;
    text-align: center;
    padding-bottom: 50px;
    text-transform: uppercase;
  }

}


@media only screen and (min-width: 600px){

  h1{
    font-size: 2.5vw;
  }

  .nav_desktop{
    display: block;
  }

  #nav_mobile{
    display: none;
  }

  .nav_btn{
    display: none;
  }

  .cta{
    padding: 25px 50px;
    background: #000;
    color: #fff;
    display: block;
    width: auto;
    margin: auto;
  }

  .cookies{
    left: 6vw;
    transform: translate(0, 0);
  }

  .banner{
    padding-top: 35vh;
  }

  .bnr_video{
    margin-top: -35vh;
  }


  .feat-section{
    .feat_video{
      min-width: 375px;
      max-width: 180%;
      margin-left: -2vw;

      video{
        width: 145%;
      }
    }

    .--video-right{
      margin-right: -3vw;
      video{
        transform: translate(-32%, 0);
      }
    }

    .feat-logo{
      width: 20vw;
    }
  }

  .payment{
    text-align: center;
    margin-top: 20vh;
  
    .text-anim{
  
      &:first-child > span{
        font-size: 5vw;
      }
    }
  
    video{
      transform: translate(5%, -30%);
      margin-bottom: -30%;
      width: 100%;
    }
  }


  .slider-logos{
    transform: scale(1);

    & > div:nth-child(even){
      margin-left: 0;
    }
  
    div{

      div{
  
        img{
          width: 33.33%;
          padding: 100px;
          margin: 50px;
        } 
      }
    }
  }


  .slider-logos div:nth-child(even){
    margin-left: 0;
  }



  .pre{
    text-align: left;

    .text-anim{
      margin-bottom: 0;
    }

    .cta{
      margin: unset;
    }

    .pre-text{
      display: flex;
      align-items: center;
      padding: 0 6vw;
      justify-content: space-between;

      & > div:last-child{
        width: unset;
        margin: unset;
      }
    }

  }

  .footer{

    .footer-heading{
      h1{
        font-size: 6vw;
      }
    }
  
    .footer-video{
      margin-top: -30%;
  
      video{
        width: 100%;
        transform: translate(0, 0);
      }
    }
  
    .footer-form{
  
      form{
        width: 80%;

        input{
          width: 80%;
        }
  
        button{
          width: 20%;
        }
  
        input, button{
          padding: 35px 45px;
          border: none;
          
        }
      }
    }
  
  
    p{
      font-size: 10px;
      text-align: center;
      padding-bottom: 50px;
    }
  
  }

}